/***********************
** Events Block on Homepage
************************/
.events {
	margin-bottom: 40px;

	h2.block-title {
		text-transform: uppercase;
		margin-bottom: 40px;
		font-family: $font-family-bodoniitalic;
	}

	.view-content {
		@media #{$normal} {
			display: flex;
			justify-content: space-between;
		}
	}

	.views-row {
		margin-bottom: 30px;
		@media #{$normal} {
			width: 30%;
		}
	}

	.views-field-field-image {
		margin-bottom: 30px;
		@media #{$normal} {
			width: 100%;
		}
	}

	.views-field-field-subtitle {
		color: $brand-contrast2;
		padding-bottom: 0;
		font-weight: normal;
		font-size: 18px;
	}

	.views-field-title {
		h3 {
			margin-bottom: 5px;
		}

		a {
			color: $brand-primary;
			font-size: 18px;
		}
	}

	.views-field-field-location {
		margin-bottom: 15px;
	}

	.views-field-body {
		font-size: 16px;
		line-height: 1.2em;
	}

	.more-link {
		float: right;
		clear: both;
	}
}
/***********************
** Events Listing Page
************************/
.view-events-page {
	.views-row {
		overflow: hidden;
		margin-bottom: 30px;
		padding-bottom: 30px;
		border-bottom: 1px solid #efefef;

		&:last-of-type {
			border-bottom: none;
		}
	}

	.views-field-field-external-url img {
		margin: 0 0 15px;
		@media #{$normal} {
			float: left;
			margin: 0 30px 15px 0;
		}
	}

	.views-field-field-event-date,
	.views-field-field-location {
		margin-bottom: 15px;
	}

	.views-field-field-event-image {
		@media #{$normal} {
			float: left;
			margin: 0 30px 20px 0;
		}
	}
}
/*******************
** Events Node
********************/
body.page-node-type-events {
	.field--name-field-image {
		margin-bottom: 50px;
	}

	.field--name-field-media-image {
		margin-bottom: 10px;
	}

	.field--name-field-caption {
		font-style: italic;
		margin-bottom: 30px;
		color: #999;
	}

	.field--name-field-location,
	.field--type-datetime {
		margin: 0 0 15px;
	}
}
