
.paragraph--type--quick-links {

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: antialiased;

  .field--type-link {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .field--name-field-link {

  
    .field--item {

      width:100%;

      @media #{$tablet} {
        width: auto;
      }

      a {
        display: block;
        position: relative;
        color: #E57200;
        font-family: $font-family-demicond;
        font-size: 1.35em;
        text-transform: uppercase;
        text-decoration: none !important;
        text-align: center;
        border: #E57200 solid 1px;
        background: none;
        margin: 0 0 1em 0;
        width:100%;
        height: 12rem;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 2rem;

        @media #{$tablet} {
          width: 350px;
        }
        @media #{$normal} {
          width: 300px;
        }
        @media #{$wide}{
          width: 270px;
        }

        &:hover{
          border: #232D4B solid 1px;
          color: #232D4B;

          &:after{
              background: linear-gradient(to right, #232D4B, #009FDF);
          }
        }

        &::after{
          content: ' ';
          display: block;
          background: linear-gradient(to right, #E57200, #F07676);
          display: block;
          height: 1rem;
          margin: 0 auto;
          width: 37.5%;
          position: absolute;
          bottom: 0;
          left: 31.25%;
        }
      }
    }
  }
}

.path-frontpage {
  .paragraph--type--quick-links {
    .field--name-field-link {
      .field--item {
        a {
          @media #{$tablet} {
                width: 350px;
              }
              @media #{$normal} {
                width: 280px;
              }
              @media #{$wide}{
                width: 320px;
              }
        }
      }
    }
  }
}


