/*********************************************
** Responsive Mobile Menus
*********************************************/

/*Mobile Menu Button Top*/
#mobile-menu {
  background: url("/sites/all/themes/global/images/mobile-menu-orange.png") 0px 0px no-repeat;
  height: 32px;
  width: 24px;
  margin-top: 20px;
  float: right;
  z-index: 999999;
  position: relative;
  margin-right: 46px;

  .glyphicon{
    display: none;
  }

  @media #{$normal}{ //display Mobile menu for mobile and tablet widths
    display: none;
  }

  p {
    display: none;
  }
}
/*Super Fish Accordion*/
.sf-accordion-toggle {
  padding: 10px;
  background-color: white;
  border-top: solid 2px $brand-contrast2;
  text-align: center;
  border-bottom: solid 2px $brand-contrast2;
  font-family: $font-family-demi;

}

ul.sf-menu.sf-accordion {
  background-color: $gray-medium;
  box-shadow: 0 0 10px 0 rgba(0,0,0,.1);


  li {
    text-align: left;
    border-bottom: solid 1px $gray-body-text;
    background-color: $gray-medium;
    text-transform: uppercase;
    float: none;
    margin: 0 auto;
    font-family: $font-family-demi;

      a {
        color:$brand-primary;
      }
      a:focus{
        text-decoration:none;
      }

      a:hover{
        text-decoration: none;
      }
      ul li{
        background: white;
        border-bottom: solid 1px #dadada;
        width: 100%;
        opacity: .9;
        transition: opacity .2s ease;

        &:hover{
          opacity: 1;
        }
      }
      ul li:last-of-type {
        border: none;
      }
  }
}
.sf-accordion li.menuparent{
  background-image: url("/sites/all/themes/global/images/caret-down-blue.png");
  background-size: 20px;
  background-position: right 45px center;
  background-repeat: no-repeat;
}
.menuparent.sf-expanded{
  background-color: white;
  a.menuparent{
    color: $gray-medium;
    background-color: $brand-primary;
    background-image: url("/sites/all/themes/global/images/caret-up-grey.png");
    background-size: 20px;
    background-position: right 45px center;
    background-repeat: no-repeat;
  }
}

ul.sf-has-clone-parent{
  background-color: white;
}
