/************************************
 * UVA Footer Area
 ***********************************/

.footer {
	background:$brand-primary;
	margin-top: 0px;
	padding-top: 40px;
	padding-bottom: 10px;
	color:#fff;
	font-size:15px;
	font-family:$font-family-sans-serif;

	a {
		color:#fff;
		text-decoration:underline;
		&:hover {
			text-decoration: none;
		}
	}
}

.region-footer {
		margin:0 auto;
		overflow:hidden;

		@media #{$wide} {
			width:$container-lg;
		}

		p {
			margin-bottom:3px;
		}
}

.footer-logo {
	text-align:center;
	margin-bottom:30px;

	@media #{$wide} {
		width:20%;
		float:left;
		text-align:left;
	}
}	

.footer-contact {
	margin-bottom:30px;
	text-align:center;

	div {
		overflow:hidden;
		&.contact.first {
			margin-right:40px;
		}
		.notice {
			display:block;
			clear:both;
			margin-top:20px;
		}
	}

	@media #{$wide} {
		width:60%;
		float:left;
		text-align:left;

		div {
			padding:0 15px;
			float:left;
			overflow:hidden;

		}
	}
}

.footer-social {
	width:140px;
	margin:0 auto;
	padding:0 20px;

	p {
		margin-bottom:6px;
	}

	@media #{$wide} {
		width:20%;
		float:left;
	}
}
