.mosaic-container {
  display: flex;
  flex-wrap: wrap;
  width: 100vw;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;

  @media #{$tablet} {
    margin-top: 2em;
    margin-bottom: 2em;
  }

  @media #{$normal} {
    flex-wrap: nowrap;
  }
}

.mosaic-wrap {
  position: relative;
  width: 100%;

    @media #{$tablet} {
      width: 33.33%;
    }
    @media #{$normal}{
      margin-top: 6em;
      margin-bottom: 6em;
    }
}

h3.mosaic-title {
  position: absolute;
  top: 1%;
  padding-left: 1em;
}

.mosaic-1 a {
  font-size: 2.5em;
  text-transform: uppercase;

  @media #{$tablet} {
    font-size: 1.5em;
  }
  @media #{$normal} {
    font-size: 1em;
  }
  @media #{$wide} {
    font-size: 1.5em;
  }
}

.mosaic-2 a {
  background: #EF3F6B;
  padding: .5em;
  font-size: 0.75em;

  &:hover {
    background: #2b466f;
  }

  @media #{$normal} {
    font-size: .5em;
  }
  @media #{$wide} {
    font-size: .75em;
  }
}

