.paragraph--type--diagonal-call-to-action {


  background: #0B265B url("/sites/all/themes/uvatemplate/images/BlueBarWithHalftone.png") no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
  width: 100vw;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
  max-height: 1000px;
  position: relative;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: antialiased;

  @media #{$wide} {
      background: #0B265B url("/sites/all/themes/uvatemplate/images/BlueBarWithHalftone_wide.png") no-repeat;
      background-size: cover;
      background-position: center;
  }

  .cta-container {

    margin: auto 2em;
    max-width: 900px;

    @media #{$tablet} {
      margin: auto 4em;
    }

    @media #{$normal} {
      margin: 0 auto;
    }

    .cta-section {

      padding: 4.5rem 13rem 3rem 0;
      background: url("/sites/all/themes/uvatemplate/images/ThreeArrows_orange.png") no-repeat right center;
      background-size: 100px 45px;

      @media #{$tablet} {
        padding: 9.375rem 26rem 9.375rem 0;
        background: url("/sites/all/themes/uvatemplate/images/ThreeArrows_orange.png") no-repeat right center;
        background-size: 152px 88px;
      }

      &:hover {
        padding: 4.5rem 13rem 3rem 0;
        background: url("/sites/all/themes/uvatemplate/images/ThreeArrowsHover_white.png") no-repeat right center;
        background-size: 100px 45px;

        @media #{$tablet} {
          padding: 9.375rem 26rem 9.375rem 0;
          background: url("/sites/all/themes/uvatemplate/images/ThreeArrowsHover_white.png") no-repeat right center;
          background-size: 152px 88px;
        }
      }

      .field--name-field-title {
        color: #fff;
        font-family: "franklin-gothic-urw", sans-serif;
        font-size: 3rem;
        font-weight: 700;
        line-height: 1.1;
        margin-bottom: 1.1rem;
      }

      .field--name-field-body {
        color: #fff;
        font-family: "franklin-gothic-urw", sans-serif;
        font-size: 1.25rem;
        font-weight: 700;
        line-height: 1.7em;
        margin: 0;
      }

      .field--name-field-link-single {
        line-height:1.3em;
        a {
          position: absolute;
          top:0;
          left: 0;
          right: 0;
          bottom: 0;
          opacity: .01;
          z-index: 999;

        }
      }
    }
  }
}
