//*******************************
// General Global styles
//*******************************

html {
  overflow-x: hidden;
}

body {
  margin-top: 0;
  font-family: $font-family-sans-serif;
  font-size: $font-size-base;
  color: $gray-body-text;
}

ol,
ul {
  padding-left: 1.5em;

  .popover &:last-child {
    margin-bottom: 0;
  }
}

h1 {
  font-family: $font-family-demi;
  font-style: normal;
  font-size: 36px;
  color: $brand-primary;
}

h2 {
  font-family: $font-family-demicond;
  font-size: 26px;
  color: $brand-primary;
  text-transform: uppercase;

  a{
    color: $brand-primary;
  }
}

h3 {
  color: $brand-primary;
  font-family: $font-family-medium;
  font-size: 20px;
  font-weight: 500;
}
b, strong{
  font-weight: normal;
  font-family: $font-family-demi;
}

h4 {
  font-family: $font-family-demi;
  font-style: normal;
  font-size: 19px;
  color: $brand-contrast;
  text-transform: uppercase;
}

a {
  color: $brand-primary;
}

.more-link {
  text-align: left;
  float: right;
  width: auto;

  a {
    padding: 8px 25px;
    color: white;
    background-color: #eb5f0c;
    text-transform: uppercase;
    font-family: 'ITC Franklin Gothic LT W01DmIt', sans-serif;
    font-size: 18px;
    font-weight: normal;
  }
}
.more-link-arrows,
.arrows {
  font-family: 'ITC Franklin Gothic LT W01DmIt', sans-serif;
  font-size: 17px;

  &::after {
    content: " >>";
  }
}
.triple-angle-link {

  color: #e57200;
  font-size: 110%;
  text-decoration: none;
  text-transform: uppercase;
  font-family:$font-family-demi;
  /*display:block;
  margin:10px 0 20px;*/

  &::before {
    content: '///';
    display: inline;
    margin-right: 0.25em;
  }
  &::after {
    content: '>';
    display: inline;
    margin-left: 0.25em;
  }
}
.main-container {
  .region-content {
    a.triple-angle-link {
      text-decoration: none;
    }
  }
}

img.align-right,
img.align-right,
img.right {
  float: right;
  margin: 0 0 20px 40px;
}

img.align-left,
img.align-left,
img.left {
  float: left;
  margin: 0 40px 20px 0;
}
/*Purge block styles*/
.block-purge-ui {
  background: #efefef;
  margin: 30px 0;
  padding: 5px 25px 15px;
}

#region-content .button {
  display: block;
  margin: 30px 0;
  text-transform: uppercase;
  width: auto;

  a {
    color: #fff;
    font-size: 18px;
    padding: 15px 25px;
    background: #232D4B;

    &:hover {
      text-decoration: underline;
    }
  }
}

.orange {
  color:$brand-contrast;
}

.box {
  background:#efefef;
  border:1px solid #ccc;
  padding:20px;
  margin:10px 0 20px;
  width:100%;
}


/**************
** To Top Button
***************/
#block-totopbutton {
  clear: both;
}

#toTop {
  position: fixed;
  bottom: 25px;
  right: 30px;
  float: right;
  display: none;
  cursor: pointer;
  width: auto;
}
/***************************
** Breadcrumb styles
****************************/
.breadcrumb {
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: 'ITC Franklin GothicLTW01BkCnIt', Arial, sans-serif;
  padding: 0;
  color: #232D4B;
  background: none;
  border-radius: none;
}

.breadcrumb a {
  color: #666;
}
//*******************************
// Paragraphs
//*******************************

.form-group:last-child,
.panel:last-child,
p:last-child {
  margin-bottom: 0;
}

.align-left{
  float: none;
  margin: 0 auto 20px;
  @media #{$tablet}{
    float: left;
    margin-right: 15px;
    margin-bottom: 15px;
    max-width: 40%;
  }
}

.align-right{
  float: none;
  margin: 0 auto 20px;
  @media #{$tablet}{
    float: right;
    margin-left: 15px;
    margin-bottom: 15px;
    max-width: 40%;
  }
}
figcaption{
  font-size: .8em;
}

.align-center{
  img{
    margin: 10px auto;
  }
  text-align: center;
  margin-bottom: 20px;
}

//*******************************
// Fancy Header
//*******************************

.inline-data-header {

  margin-bottom:2.5em;
  display: flex;
  -webkit-box-pack: center;
  justify-content:center;
  -webkit-box-flex: 1;
  flex: 1 1 auto;

  .data-header-design-element {
    align-self: center;
    background-color: #979797;
    -webkit-box-flex: 1;
    flex: 1;
    height: 1px;
  }
  .data-header-title-wrapper {

    transform: skew(-20deg);
    border-left: 1px solid #979797;
    border-right: 1px solid #979797;
    display: inline-block;
    -webkit-box-flex: 0;
    flex: 0 auto;

    h2 {
      transform: skew(20deg);
      margin: 0;
      padding: 0 1.25em;
      text-align: center;
      text-transform: uppercase;
    }
  }
}



//*******************************
// Webforms
//*******************************

.form-actions .btn, .form-actions .btn-group {
    margin-top: 30px;
}
.views-exposed-form .form-actions .btn, .views-exposed-form .form-actions .btn-group {
    margin-top: 0px;
}
.webform-submission-form {
  .form-type-textarea {
    margin-bottom:15px;
  }
  fieldset.radios--wrapper {
    legend {
      font-size:17px;
      color:#666;
      margin-bottom:5px;
    }
    margin-bottom:15px;
  }
}



