/************************************
 * UVA Slideshow Area
 ***********************************/


/**Container**/
.preface {
	padding:0 0;
	background:#fff;
	font-size:36px;
	color:$brand-primary;
	text-align:center;
}

.slick {
	max-width: 100%;

	img {
		//this presents a better initial image on homepage for mobile; if it doesn't work
		//for specific images, override with img width 100% (commented out above).
		object-fit:cover;
		font-family: 'object-fit:cover;';
		height:350px;
		width:100%;


		@media #{$tablet} {
			width: 100%;
			height:auto;
		}
	}

	&:hover .slick-arrow::before{
		opacity: 1;
	}

}

/**** Change arrows **/
.slick__arrow{
	display: block !important;
}
.slick-arrow{

	&::before{
		font-size: 5rem;
		color: #eb5f0c;
		opacity: 0;
		transition: all .4s ease-out;
	}

	&:hover::before{
		color: #141e3c;
		font-size: 6rem;
	}

	&.slick-next{
		right: 20px;
	}
	&.slick-prev{
		left: 20px;
	}
}

/************************************
 * Slideshow caption
 ***********************************/
.slide__constrained {

	background:#fff;
	min-height:0;
	padding:3%;

	@media #{$normal} {
		background:none;
	}

	.slide__caption{
		overflow:hidden;
	  	width: 100%;
	  	text-align:left;
	  	border-bottom:1px solid #efefef;

		h2.slide__title{
			font-size:24px;
			padding:0 0 15px;
		}
		a {

			&:hover {
				text-decoration:underline;
			}
		}
		p {
			font-size:16px;
		}

		@media #{$tablet} {
			h2.slide__title{
				font-size:36px;
			}
		}
		@media #{$normal} {
			position:absolute;
			right: 8%;
		  	bottom: 5%;
		  	left: auto;
		  	top: auto;
		  	width: 50%;
		  	border-bottom:0 none;
		  	background: url("/sites/all/themes/global/images/white-trans.png") repeat;
		}
		@media #{$wide} {
			right: 2%;
			width:40%;
			padding:3%;
		}
	}
}
