/**********************
*** Paragraphs General
**********************/

  .field--name-field-paragraphs {
    padding-bottom: 35px;
    @media #{$normal} {
      padding: 0 50px 35px;
      margin: 0 -50px;
    }

  }

  .paragraph {
    margin-bottom: 25px;

    @media #{$normal} {
    margin-bottom: 45px;
    }
  }


/******************
** Large Callout Text
*******************/
.paragraph--type--large-callout-text {
  font-size: 1.3em;
  font-family: $font-family-demi;
  margin-bottom: 30px;
}

/*****************
** Orange Seperator
*******************/
.paragraph--type--short-orange-seperator{
  height: 5px;
  width: 50px;
  border-bottom: solid 2px $brand-contrast;
}

/*****************
** Blue Button
*******************/
.field--name-field-button-link{
  margin-top: 30px;
  a{
    font-family: $font-family-sans-serif;
    padding: 15px 50px;
    text-transform: uppercase;
    background-color: $brand-primary;
    color: white;
    transition: background-color .8s ease;

    &:hover{
      text-decoration: none;
      background-color: lighten(#232D4B, 10);
    }
  }
}

/*****************
** Videos
****************/
.paragraph--type--video-embed {

  @media #{$normal}{
    margin:  auto -50px 30px;
  }
}

/*****************
** Big Text - for page intros, etc.
****************/
.paragraph--type--big-text {
  font-size:20px;
  color:$brand-primary;

  @media #{$tablet}{
    font-size:24px;
  }
}
