.paragraph--type--curated-links{
  font-size: 1.3em;
  position: relative;
  margin: 30px 0 50px;

  .field--name-field-link {
    display: flex;
    flex-wrap: wrap;
    position: relative;
  }

  .field--item{
    display: block;
    width: 100%;
    border-bottom: solid 2px #ddd;
    margin: 0 5%;
    position: relative;

    @media #{$tablet}{
      width: 40%;
    }
  }

  a {
    display: block;
    padding: 20px 20px 20px 0;
    font-family: $font-family-compressed;

    &::after{

      border-style: solid;
      border-color: $brand-contrast;
      border-width: 0 5px 5px 0;
      display: block;
      padding: 5px;
      position: absolute;
      right: 0;
      top: calc(50% - 5px);
      transform: rotate(-45deg);
      -webkit-transform: rotate(-45deg);
      content: ' ';
      transition: all .8s ease;
    }

    &:hover{
      &::after{
        border-color: $brand-primary2;
      }
    }
  }

  &::after{
    @media #{$tablet}{
      position: absolute;
      top: 10px; bottom: -10px; left: calc(50% - 1px); right: calc(50% - 1px);
      background-color: #ddd;
      content: ' ';
      display: block;
    }

  }
}

body .region-content .content .paragraph--type--curated-links a {
  text-decoration: none;
}
