/***************************
** UVA Header/Logo Region
****************************/

.logo {
  background: $brand-primary;
  color: #fff;
  border-top: solid 8px $brand-contrast;
}

.region-logo {
  width: 100%;
  max-width: $container-lg;
  padding: 0 1em;
  margin: 0 auto;
  overflow: hidden;
  @media #{$normal} {
    max-width: $container-md;
  }
  @media #{$wide} {
    padding: 0;
    max-width: $container-lg;
  }
}

.uva-logo-block {
  float: left;
  padding: 20px 0;

  img {
    max-width: 300px;
    height: auto;
    @media #{$mobile} {
      width: 100%;
      max-width: 500px;
    }
    @media #{$tablet} {
      max-width: 400px;
    }
    @media #{$normal} {}
    @media #{$wide} {}
  }
}
/**************************************
** Search Area
***************************************/
.search-toggle-block {
  width: auto;
  float: right;
  margin-right: 0;

  &.legacy{
    #search-toggle-button {
      margin-top:25px;
    }
    .search-close {
      padding-bottom:12px;

      @media #{$tablet} {
        padding-bottom:20px;
      }
    }
  }
}

#search-toggle-button {
  float: right;
  border: none;
  cursor: pointer;
  color: #fff;
  font-size: 17px;
  height: 25px;
  width: auto;
  padding-right: 65px;
  margin-right: 0;
  outline: 0;

  p {
    font-family: $font-family-sans-serif-alt;
    font-size: 12px;
    font-style: italic;
    font-weight: 900;
    padding-top: 0.3em;
    text-transform: uppercase;
    display: none;
    @media #{$normal} {
      display: block;
    }
  }

  &.search-close-button {
    width: 30px;
    @media #{$normal} {
      width: 90px;
    }

    p {
      display: none;
    }
  }
}

.search-close {
  background: #fff;
  border-bottom: dotted 2px $brand-primary;
}

.search-open-button {
  background: rgba(0, 0, 0, 0) url("/sites/all/themes/global/images/search-icon-white.png") no-repeat scroll right 20px top;
  @media #{$normal} {
    background: rgba(0, 0, 0, 0) url("/sites/all/themes/global/images/search-icon-white.png") no-repeat scroll right 33px top;
  }
}

.search-close-button {
  background: rgba(0, 0, 0, 0) url("/sites/all/themes/global/images/blue-cross.png") no-repeat scroll right 20px top;
  @media #{$normal} {
    background: rgba(0, 0, 0, 0) url("/sites/all/themes/global/images/blue-cross.png") no-repeat scroll right 33px top;
  }

  p {
    display: none;
    @media #{$normal} {
      display: none;
    }
  }
}
/*********************************************
** Toggling Search Bar
*********************************************/
.search {
  display: none;

  .form-submit {
    font-family: 'ITC Franklin GothicLTW01BkCnIt';
    background-color: $brand-contrast;
    border: 1px solid $brand-contrast;
    border-radius: 0;
    display: inline-block;
    cursor: pointer;
    color: #ffffff;
    font-size: 19px;
    padding: 6px 10px;
    text-decoration: none;
    text-shadow: 0 1px 0 #b23e35;
    letter-spacing: 1px;
    width: 90px;
    height: 50px;
    float: right;
    margin-right: 16px;
    border-radius: 0;
    @media #{$wide} {
      margin-right: 0;
    }
  }

  .form-group {
    margin-bottom: 0;
  }
}

#block-uvatemplate-search {
  margin: 0 auto;
  width: 100%;
  @media #{$normal} {
    width: $container-md;
  }
  @media #{$wide} {
    width: $container-lg;
  }
}

.region-search .form-search {
  font-family: $font-family-demicond;
  font-size: $font-size-h3;
  color: #5e5e5e;
  background: none repeat scroll 0 0 white;
  border: none;
  padding: 5px;
  width: 100%;
  box-sizing: border-box;
  margin: 0;
  height: 50px;

  &::-webkit-input-placeholder {
    color: $brand-primary;
    opacity: 0.9;
  }

  &:-moz-placeholder {
    /* Firefox 18- */
    color: $brand-primary;
    opacity: 0.9;
  }

  &::-moz-placeholder {
    /* Firefox 19+ */
    color: $brand-primary;
    opacity: 0.9;
  }

  &:-ms-input-placeholder {
    color: $brand-primary;
    opacity: 0.9;
  }
}
