/*********************************************
** Main Container Area
*********************************************/
.main-container {
  margin-top: 10px;
  margin-bottom: 40px;

  h1.page-header {
    margin: 15px 0 22px;
    padding-bottom: 10px;
  }

  .col-sm-12.subpage-header {
    padding-left: 0;
    padding-right: 0;
  }

  .region-content {
    clear: both;
  }
  @media #{$normal} {
    margin-top: 40px;
  }
  @media #{$wide} {
    .col-sm-9.col-sm-12 {
      padding-left: 0;
      padding-right: 0;
    }
  }
}

.path-frontpage {
  .main-container {
    margin-top: 0;
  }
}

hr {
  margin-top: 30px;
  margin-bottom: 30px;
}

hr.blue-dotted {
  background: url("/sites/all/themes/global/images/blue-dot.png") center left repeat-x;
  height: 8px;
  border: 0;
}
