.paragraph--type--three-column-layout {
	
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin: 40px auto 30px;


	@media #{$normal}{
		margin: auto 0 30px;
	}
	
    .field--name-field-col-1,
	.field--name-field-col-2,
	.field--name-field-col-3 {

		width:100%;
		margin-bottom:40px;
		
		@media #{$normal}{
			width:31%;
		}

		img {
			width:100%;
			height:auto;
			padding-bottom:10px;

			@media #{$tablet}{
				width:auto;
			}
		}
		
		.field--name-field-event-date {
			font-weight:bold;
			padding-top:20px;
		}
		.field--name-field-location {
			font-style:italic;
		}
		.field--name-body {
			padding:20px 0 10px;
		}
		.node-readmore,
		.field--name-field-external-url {
			font-weight:bold;

			&::after {
				content:" >";
			}
		}
	}
    
}
