
/**********************************/
/* 2020- Yellow Important Notice Block used for COVID-19
***********************************/

.important-notice-block {
	clear:both;
	display:block;
	background-color: #FDDA24;
	color: #232D4B;
	font-size:17px;
	text-align:left;

	.field--name-body {
		padding-left: 70px;
		padding-top: 20px;
		background-image: url(/sites/all/themes/global/images/blue_ex.png);
		background-repeat: no-repeat;
		background-position: left 0px top 25px;
		background-size: 40px;
		max-width: 1170px;
		margin: 0 auto;
		font-size: 1.1em;
		box-sizing: border-box;
		padding-bottom: calc(20px - .5em);
	}

	&.lighter {
		background-color: #FFF2D1;

		.field--name-body {
			padding-left: 20px;
			background:none;

			@media #{$wide} {
				padding-left:0;
			}

		}
	}

	em {
		font-size: .8em;
	}
	p {
		margin-bottom: .5em;
		line-height:1.2em;

	}
	strong {
		font-weight: normal;
		font-family: 'ITC Franklin Gothic LT W01DmCn', Arial, sans-serif;
		font-size: 1.2em;
	}
	a {
		font-weight: normal;
		font-family: 'ITC Franklin Gothic LT W01DmCn', Arial, sans-serif;
		text-decoration: underline;
		color: #232D4B;

	}
}


/**********************************/
/*Emergency Alert Content type on homepage
***********************************/

.emergency-alert, .important-notice {
	background:#fff;
	color:#232D4B;
	font-size:18px;
	border:10px solid #f46C14;

	a {
		color:#232D4B;
		text-decoration:underline;

		&:hover {
			text-decoration:none;
		}
	}

	.views-row {
		margin:0 auto;
		width:93%;
		padding:20px;

		@media #{$normal} {
			width:940px;
		}
		@media #{$wide} {
			width:1180px;
		}
	}

	h2 {
		color:#232D4B;
		margin-top:0;

		&.block-title {
			font-size:20px;
			text-transform:uppercase;
			color:#fff;
			margin-bottom:10px;
			padding:4px 0 0 35px;
			background: url("/sites/all/themes/global/images/2015-template/alert-white-sm.png") center left no-repeat;
		}
	}
	.views-field-field-external-url h2 {
		margin-bottom:6px;
	}
	.views-field-field-external-url a:hover  {
		text-decoration:underline;
	}
	.title {
		font-size:28px;
		padding-bottom:8px;
		color:#232D4B;
	}
	.views-field-field-link {
		color:#232D4B;
		
		a {
			font-weight:bold;
			&::after {
				content: " >>";
			}
		}
		
	}
	.views-field-edit-node {
		clear:both;
	}
	.views-field-field-alert-date {
		padding-bottom:12px;
		font-style:italic;
	}
	.views-field-field-description {
		padding-bottom:12px;
	}
}


/**********************************/
/*Important Notice override on homepage
***********************************/
.important-notice {
	background:#FDF7E6;
	color:#232D4B;
	font-size:18px;
	border:0 none;

	h2 {
		font-size:30px;
	}
}

