/************************************
 * UVA Slideshow Area
 ***********************************/

$mobile:          "screen and (max-width: #{$screen-xs-max})";
$tablet:          "screen and (min-width: #{$screen-sm-min})";
$normal:          "screen and (min-width: #{$screen-md-min})";
$wide:            "screen and (min-width: #{$screen-lg-min})";
$grid-breakpoint: "screen and (min-width: #{$grid-float-breakpoint})";


/******************************
** Related Links Blue Boxes
*******************************/
.related-links {
	margin-bottom: 50px;

	h2.block-title {
		margin-bottom: 35px;
		padding-top: 40px;
		text-transform: uppercase;
	}
	.views-row {
		overflow: hidden;
		margin: 0 0 15px 0;
		text-align: center;

		.views-field-field-link {
			background: $brand-primary;
			padding: 1.3em 12px;
			text-transform: uppercase;
			font-family: $font-family-demicond;
			font-size: $font-size-base;
			a {
				color: #fff;
			}
			&:hover {
				background:$brand-primary;
			}
		}
	}
	@media #{$tablet} {
		.views-row {
			overflow: hidden;
			width: 46.3%;
			float: left;
			margin: 0 50px 30px 0;
			text-align: center;
		}
		.views-row:nth-child(odd) {
			clear: both;	
		}
		.views-row:nth-child(even) {
			margin: 0 0 30px 0;
		}
	}
	@media #{$normal} {
		.views-row {
			width: 29%;
			margin: 0 60px 30px 0;
		}
		.views-row:nth-child(odd) {
			clear: none;
		}
		.views-row:nth-child(even) {
			margin:0 60px 30px 0;
		}
		.views-row:nth-child(3n+0) {
			margin-right:0;
		}
		.views-row:nth-child(3n+1) {
			clear:both;	
		}
	}
	@media #{$wide} {
		.views-row {
			width: 28%;
			margin: 0 90px 30px 0;
		}
		.views-row:nth-child(even) {
			margin:0 90px 30px 0;
		}
		.views-row:nth-child(3n+0) {
			margin: 0 0 30px 0;
		}
	}
}


