/*****************
** Image General
*******************/
.field--name-field-caption {
  padding: 10px 0;
  @media #{$normal} {
    padding: 20px 0 25px;
  }
  font-size: 0.9em;
}
/*****************
** Image FW
*******************/
.paragraph--type--full-width-image {

  @media #{$normal} {
    margin: 0 -50px 45px;
  }

  img {
    margin:0 auto;
  }

  .field--name-field-caption {
    text-align: center;
    margin: 0 auto;
    padding-bottom: 0;
    @media #{$normal} {
      width: calc(100% - 50px);
    }

    &::after {
      content: " ";
      width: 85%;
      height: 2px;
      background-color: $gray-light;
      margin: 25px auto 0;
      display: block;
    }
  }
}
/**********************
** Image Text Right
***********************/
.paragraph--type--image-text-right {
  .field--name-field-image,
  .field--name-field-media-image {
    margin-top: 20px;
  }
  @media #{$normal} {
    overflow: hidden;
    margin-left: -50px;

    .field--name-field-caption {
      padding: 20px 10px;
      background-color: $gray-light;
    }
  }
}

/**********************
** Image Text Left
***********************/
.paragraph--type--image-text-left {
  .field--name-field-image,
  .field--name-field-media-image {
    margin-top: 20px;
  }
  @media #{$normal} {
    overflow: hidden;
    margin-right: -50px;

    .field--name-field-caption {
      padding: 20px 10px;
      background-color: $gray-light;
    }
  }
}

.two-wide {
  @media #{$normal} {
    display: flex;
    justify-content: space-between;
  }

  .group-left,
  .group-right {
    @media #{$normal} {
      width: 48%;

      img{
        margin-top: -17px;
      }
    }
  }
}
