/**
 * UVA Navbar styling.
 */
$mobile:          "screen and (max-width: #{$screen-xs-max})";
$tablet:          "screen and (min-width: #{$screen-sm-min})";
$normal:          "screen and (min-width: #{$screen-md-min})";
$wide:            "screen and (min-width: #{$screen-lg-min})";
$grid-breakpoint: "screen and (min-width: #{$grid-float-breakpoint})";

body {
  // Fix horizontal scrolling on iOS devices.
  // http://drupal.org/node/1870076
  position: relative;

  &.navbar-is-static-top {
    margin-top: 0;
  }
  &.navbar-is-fixed-top {
    margin-top: ($navbar-height + ($grid-gutter-width / 2));
  }
  &.navbar-is-fixed-bottom {
    padding-bottom: ($navbar-height + ($grid-gutter-width / 2));
  }

  @media #{$tablet} {
    //margin-top: ($grid-gutter-width / 2);
    //margin-top:0;
  }
  @media #{$mobile} {
    &.toolbar-vertical {
      &.navbar-is-fixed-top,
      &.navbar-is-fixed-bottom {
        .toolbar-bar {
          position: fixed;
        }
        header {
          z-index: 500;
        }
      }
      // Default toolbar fixed height value.
      // @see core/modules/toolbar/css/toolbar.icons.theme.css@261
      &.navbar-is-fixed-top header {
        top: 39px;
      }
    }
  }
}

.navbar.container {
  @media #{$tablet} {
    max-width: (($container-sm - $grid-gutter-width));
  }
  @media #{$normal} {
    max-width: (($container-md - $grid-gutter-width));
  }
  @media #{$wide} {
    /*max-width: (($container-lg - $grid-gutter-width));*/
    max-width: (($container-lg));
  }
}

// Branding.
.navbar .logo {
  margin-right: -($grid-gutter-width / 2);
  padding-left: ($grid-gutter-width / 2);
  padding-right: ($grid-gutter-width / 2);
  @media #{$grid-breakpoint} {
    margin-right: 0;
    padding-left: 0;
  }
}

.navbar {
  margin-bottom:0;
}

.navbar-header {
  
  @media #{$tablet} {
    float: right;
    width: 100%;
  }
}

.navbar-default {
  display: none;

  @media #{$tablet} {
    background-color: #fff;
  }
  @media #{$normal} {
    background-color: $gray-medium;
    border:0 none;
    border-radius:0;
    display: block !important; //hide main horizontal navbar until Normal width
  }

  .container {
    padding-left:0;
    padding-right:0;


    /*Regular menu, no dropdown*/
    .navbar-nav {

      margin:0 10px;

      @media #{$tablet} {
        margin:0;
      }

      & > li > a, & > .active > a, & > .active > a:focus {
        padding: 14px 30px;
        color:$brand-primary;
        font-family:$font-family-demicond;
        font-size:16px;
        text-transform:uppercase;
      }
      & > li > a:hover, & > .active > a:hover {
        background:white;
      }

      .dropdown-menu {
        min-width:180px;
        padding:0;
        font-family:$font-family-demicond;
        font-size:16px;
        & > li > a {
            padding: 10px 20px;
            color: $brand-primary;
            text-transform:uppercase;
        }
        & > .active > a, & > .active > a:hover, & > .active > a:focus {
            color: $brand-primary;
            background-color: $gray-light;
        }
      }
    }

    /*styling for Dropmenus */
    #superfish-main {

      & > li > a, & > .active > a, & > .active > a:focus {
        padding: 17px 30px;
        color:$brand-primary;
        font-family:$font-family-demicond;
        font-size:16px;
        text-transform:uppercase;
      }
      & > li > a:hover, & > .active > a:hover, & > .menuparent:hover {
        background:white;
      }

      li ul {
        top:50px;
        box-shadow: 5px 7px 5px -5px rgb(220, 221, 222);

        li {
          background:white;
          text-transform: uppercase;
          color: #232d4b;
          font-family: $font-family-demicond;
          font-size:15px;
          &:hover {
            background:$gray-medium;
          }
        }
        ul {
          top:0;

          li {
            background:#efefef;
          }
        }
      }
    }
  }
}
