

/*****************
** Quote FW
*******************/

.paragraph--type--quote {

    padding: 1.7em 0;
    margin:50px;
    margin:50px 10px;
    background: url("/sites/all/themes/uvatemplate/images/template-quotes.png") no-repeat center top;
    background-size: 300px 96px;
    text-align:center;
    color:$brand-primary;
    line-height:1.3em;
    
    @media #{$tablet}{
      margin:50px;
    }

    @media #{$normal}{
      margin:30px 100px 0;
    }

    .field--name-field-quote-text {
      font-size:2em;
      line-height:1.3em;
      font-family:"ITC Franklin Gothic LT W01DmXC", sans-serif;
      font-family:$font-family-demiextracomp;
      

      @media #{$normal}{
        font-size: 2.8em;

      }
    }
    .field--name-field-quote-author {
      padding:30px 15px 0;
      margin:60px auto 0;
      border-top:6px solid $brand-contrast;
      width:auto;
      display:inline-block;
      font-size:20px;
      font-family:$font-family-compressed;
    }
}




