//*******************************
//Colors
//*******************************

//Primaries
$brand-primary:         #232D4B;
$brand-primary2:		#141E3C;
$brand-contrast:		#E57200;
$brand-contrast2:		#EB5F0C;
$brand-warning:         #E57200;
$brand-danger:          #DF1E43;
$brand-emergency:       #DF1E43;

//Grays
$gray-base:              #000;
$gray-lighter:           lighten($gray-base, 93.5%); // #eee
$gray:                   #ccc;
$gray-dark:              lighten($gray-base, 20%);   // #333

//UVA
$gray-light:             #f1f1ef;
$gray-medium:            #dadada;
$gray-body-text:		 #666;

//Black Tints
$black: #000;
$black75: tint($black, 75%);
$black50: tint($black, 50%);
$black25: tint($black, 25%);

/*lighter blue link color*/
$link-light:		#007bac;

/****************************
** Accents
****************************/

$accent-cyan: #009FDF;
$accent-cyan75: tint($accent-cyan, 75%);
$accent-cyan50: tint($accent-cyan, 50%);
$accent-cyan25: tint($accent-cyan, 25%);

$accent-yellow: #FDDA24;
$accent-yellow75: tint($accent-yellow, 75%);
$accent-yellow50: tint($accent-yellow, 50%);
$accent-yellow25: tint($accent-yellow, 25%);

$accent-teal: #25CAD3;
$accent-teal75: tint($accent-teal, 75%);
$accent-teal50: tint($accent-teal, 50%);
$accent-teal25: tint($accent-teal, 25%);

$accent-magenta: #EF3F6B;
$accent-magenta75: tint($accent-magenta, 75%);
$accent-magenta50: tint($accent-magenta, 50%);
$accent-magenta25: tint($accent-magenta, 25%);

$accent-green: #62BB46;
$accent-green75: tint($accent-green, 75%);
$accent-green50: tint($accent-green, 50%);
$accent-green25: tint($accent-green, 25%);

//*******************************
//Global Styles, Hyperlink Colors
//*******************************

//** Background color for `<body>`.
$body-bg:               #fff;


//*******************************
// Typography
//*******************************
//## Font, line-height, and color for body text, headings, and more.

$font-family-sans-serif:  'ITC Franklin Gothic LT W01 Bk', Arial, sans-serif;
$font-family-sans-serif-alt: Arial, sans-serif;
$font-family-serif:       Georgia, "Times New Roman", Times, serif;
$font-family-base:        $font-family-sans-serif;

$font-family-cond: 'ITC Franklin Gothic LT W01 BkC', sans-serif;
$font-family-demi:  "ITC Franklin Gothic LT W01 Dm", Arial,sans-serif;
$font-family-demicond:  "ITC Franklin Gothic LT W01DmCn", Arial,sans-serif;
$font-family-demiital:  "ITC Franklin Gothic LT W01DmIt", Arial,sans-serif;
$font-family-demicondital:  "ITC Franklin GothicLTW01DmCnIt", Arial,sans-serif;
$font-family-medium: 'ITC Franklin Gothic LT W01 Md', Arial, sans-serif;
$font-family-heavy:  "ITC Franklin Gothic LT W01 Hv", "ITC Franklin Gothic LT W01 Dm", sans-serif;
$font-family-compressed: 'ITC Franklin Gothic LT W01 BkC', Arial, sans-serif;
$font-family-demicompressed: "ITC Franklin Gothic LT W01 DmC", "ITC Franklin Gothic LT W01 Dm", Arial, sans-serif;
$font-family-demicompital: "ITC Franklin Gothic LTW01DmCmI", sans-serif;
$font-family-demiextracomp: "ITC Franklin Gothic LT W01DmXC", sans-serif;


$font-family-bodoni:	'BodoniW01-PosterItalic', serif;
$font-family-bodoniitalic:	'BodoniW01-PosterItalic', serif;
$font-family-page-header:	'ITC Franklin Gothic LT W01DmCn', serif;

$font-size-base:          16px;
$font-size-large:         ceil(($font-size-base * 1.25)); // ~18px
$font-size-small:         ceil(($font-size-base * 0.85)); // ~12px

$font-size-h1:            floor(($font-size-base * 2.6)); // ~36px
$font-size-h2:            floor(($font-size-base * 2.6)); // ~36px
$font-size-h3:            ceil(($font-size-base * 2.15)); // ~30px
$font-size-h4:            ceil(($font-size-base * 1.7)); // ~24px
$font-size-h5:            $font-size-base;
$font-size-h6:            ceil(($font-size-base * 0.85)); // ~12px

//** Unit-less `line-height` for use in components like buttons.
$line-height-base:        1.428571429; // 20/14
//** Computed "line-height" (`font-size` * `line-height`) for use with `margin`, `padding`, etc.
$line-height-computed:    floor(($font-size-base * $line-height-base)); // ~20px

//** By default, this inherits from the `<body>`.
$headings-font-family:    inherit;
$headings-font-weight:    500;
$headings-line-height:    1.1;
$headings-color:          inherit;


//*******************************
// Container Sizes
//*******************************
//
//## Define the maximum width of `.container` for different screen sizes.

$grid-gutter-width:         30px;

// Small screen / tablet
$container-tablet:             (720px + $grid-gutter-width);
$container-sm:                 $container-tablet;

// Medium screen / desktop
$container-desktop:            (940px + $grid-gutter-width);
$container-md:                 $container-desktop;

// Large screen / wide desktop
/*$container-large-desktop:      (1140px + $grid-gutter-width);*/
$container-large-desktop:      (1170px + $grid-gutter-width);
$container-lg:                 $container-large-desktop;

// Media Queries

$mobile: "screen and (max-width: #{$screen-xs-max})";
$tablet: "screen and (min-width: #{$screen-sm-min})";
$normal: "screen and (min-width: #{$screen-md-min})";
$wide: "screen and (min-width: #{$screen-lg-min})";
$grid-breakpoint: "screen and (min-width: #{$grid-float-breakpoint})";
