/**
 * Node styling.
 */

.node-preview-container {
  margin-top: -($grid-gutter-width / 2);
}

.node-preview-form-select {
  padding: ($grid-gutter-width / 2);
}


h1.page-header {
	color: $brand-primary;
    font-family: $font-family-demi;
    font-size: 40px;
    font-style: normal;

    @media #{$normal}{
      font-size: 60px;
    }
}


/***************************
** Region Content, Embedded Images on Node Pages
****************************/
.page-node-type-page {
  .region-content {

  	padding:25px 0;
	clear:both;

    .field--name-body {
      img {
        max-width:100%;
        margin-bottom:20px;
      }
    }
  }
}

