/************************************
 * Homepage Custom Blocks - Sunbursts
 ***********************************/

.sunburst-stats {
	font-size: 24px;
	font-family: $font-family-demicond;
	color: $brand-primary;
	text-transform: uppercase;
	display: block;
	margin: 20px auto;
	overflow: hidden;
	width: 90%;
	font-weight: 100;
	overflow:hidden;

	span {
		font-size: 110px;
		font-family: $font-family-bodoniitalic;
		color: $brand-primary;
		display: block;
		clear: both;
		margin-bottom: -15px;
	}
	ul.sunburst {
		margin: 0 auto;
		padding: 0;

		li {
			background: url("/sites/all/themes/global/images/sunburst.png") center top no-repeat;
			display: block;
			width: 100%;
			text-align: center;
			vertical-align: top;
			padding: 35px 0 75px;
		}
	}

	@media #{$normal} {
		width:85%;

		span {
			font-size:90px;
		}
		ul.sunburst {
			margin:0 auto;
			padding:0;
			li {
				background-size:90%;
				display: inline-block;
				width:32%;
				padding:50px 40px 50px;
			}
		}
	}

	@media #{$wide} {
		margin: 40px auto;

		span {
			font-size:110px;
		}
		ul.sunburst {
			li {
				background-size:auto;
				width:32%;
				padding:40px 35px 35px;
			}
		}
	}
}
