/***********************
** Article Block on Homepage
************************/
.articles {
	margin: 60px 0 40px;
	padding-top: 30px;
	background: rgba(0, 0, 0, 0) url("/sites/all/themes/global/images/blue-dot.png") repeat-x left top;

	h2.block-title {
		text-transform: uppercase;
		margin-bottom: 40px;
		font-family: $font-family-bodoniitalic;
		@media #{$normal} {
			text-align: center;
		}
	}

	.views-row {
		overflow: hidden;
		margin-bottom: 30px;
	}

	.views-field-field-external-url img {
		margin-bottom: 30px;
		@media #{$tablet} {
			float: left;
			margin: 0 30px 20px 0;
		}
	}

	.views-field-title {
		h2 {
			margin-bottom: 5px;
		}

		a {
			color: $brand-primary;
			font-size: 18px;
		}
	}

	.views-field-body {
		font-size: 16px;
		line-height: 1.2em;
	}

	.more-link {
		float: right;
		clear: both;
	}
	@media #{$normal} {
		margin: 60px 100px 20px;
	}
	@media #{$wide} {
		margin: 60px 150px 20px;
	}
}
/***********************
** Articles Listing Page
************************/
.view-articles-page {
	.views-row {
		overflow: hidden;
		margin-bottom: 30px;
		padding-bottom: 30px;
		border-bottom: 1px solid #efefef;
	}

	.views-field-field-external-url img {
		margin: 0 0 15px;
		@media #{$normal} {
			float: left;
			margin: 0 30px 15px 0;
		}
	}
}
/*******************
** Articles Node
********************/
body.page-node-type-article {
	.region-content {
		.field--name-field-image {
			margin-bottom: 50px;

			img {
				display: block;
				margin: 0 auto;
			}
		}

		.field--name-field-caption {
			font-style: italic;
			padding-top: 10px;
		}

		.field--name-body {
			margin-top: 50px;
			@media #{$normal} {
				margin: 0 100px;
			}
			@media #{$wide} {
				margin: 50px 180px;
			}
		}

		p {
			@media #{$normal} {
				line-height: 1.7em;
			}
		}
	}
}
