/************************************
 * Homepage News & News Page
 ***********************************/

/***********************
** News Block on Homepage
************************/
.news {
	margin-bottom: 40px;

	h2.block-title {
		text-transform: uppercase;
		margin-bottom: 40px;
		font-family: $font-family-bodoniitalic;
	}

	.view-content {
		@media #{$normal}{
			display: flex;
			justify-content: space-between;
		}
	}

	.views-row {
		margin-bottom: 30px;

		@media #{$normal}{
			width: 30%;
		}
	}

	.views-field-field-image,
	.views-field-field-media-image {
		margin-bottom: 30px;
	}


	.views-field-field-subtitle {
		color: $brand-contrast2;
		padding-bottom: 0;
		font-weight: normal;
		font-size: 18px;
	}

	.views-field-field-external-url,
	.views-field-title {

		h3 {
			margin-bottom: 5px;


			a {
				color: $brand-primary;
				font-size: 18px;
				
			}
		}
	}

	.views-field-body {
		font-size: $font-size-base;
		line-height: 1.7em;
	}

	.more-link {
		float: right;
		clear: both;
	}

}
/***********************
** News Listing Page
************************/
.view-news-page {
	.views-row {
		overflow: hidden;
		margin-bottom: 30px;
		padding-bottom: 30px;
		border-bottom: 1px solid #efefef;

		.views-field-field-image,
		.views-field-field-media-image,
		.field--name-field-media-image {
			@media #{$normal}{
				float: left;
			}

			img {
				margin: 0 0 15px;

				@media #{$normal}{
					margin: 0 30px 15px 0;
				}
			}
		}
	}
}


/*******************
** News Node
********************/
body.page-node-type-news {
	.field--name-field-caption {
		font-style: italic;
		padding-top: 10px;
	}

	.field--name-body {
		margin-top: 50px;
	}
}
/***********************
** News (Centered, No Image) Block
************************/
.news-centered {
	margin-bottom: 0;

	.views-row {
		overflow: hidden;
		margin-bottom: 30px;
	}
}
/***********************
** News (Centered, No Image) Narrow
************************/
.news-centered {
	margin: 0 15%;
}
/*normal*/
.news-centered {
	margin: 0 20%;
}
