/************************************
 * Homepage Custom Blocks - Quote
 ***********************************/

.quote {
	margin: 15px auto 40px;
	padding: 0;
	font-size: 30px;
	font-family: $font-family-bodoniitalic;
	color: $brand-contrast2;
	text-align: center;
	text-transform: uppercase;
	overflow: hidden;

	p {
		width: 80%;
		margin: 0 auto;
		line-height: 1.2em;
		padding:30px 0 0px;
	}

	@media #{$normal} {
		margin:15px auto 30px;
		padding:0;
	}
}
.quote-author {
	font-family:$font-family-sans-serif;
	padding:20px 0 30px;
}
