/************************************
 * UVA Call to Action Area
 ***********************************/

.postscript {
	background: url("/sites/all/themes/global/images/call-to-action-background-oversized.jpg") -55px -30px repeat;
}

.region-postscript {
	@media #{$wide} {
		width: 800px;
		margin: 0 auto;
	}
}

.call-to-action {
	text-align: center;
	padding: 50px 0 70px;

	p {
		padding: 0 40px 12px;
		font-size: 22px;
		line-height: 1.4em;
		color: $brand-primary;
		font-family: $font-family-demi;
	}

	h2 {
		font-size: 78px;
		vertical-align: top;
		text-transform: uppercase;
		color: $brand-primary;
		margin-bottom: 15px;
		font-family: $font-family-demiital;

		span {
			text-decoration: underline;
			font-size: 50%;
			color: $brand-primary;
			font-family: $font-family-bodoniitalic;
		}
	}

	div.button {
		padding: 10px;
		font-family: $font-family-demiital;
		font-size: 18px;
		background: url("/sites/all/themes/global/images/2015-template/button-background.png") left repeat;
		background: $brand-primary;
		margin: 0 auto;
		width: 180px;

		a {
			color: #fff;
		}

		&:hover {
			background: #00448E;
		}
	}
}
