/*********************************************
** Branding, Title
*********************************************/
.branding {
  background: #fff;
  color: $brand-primary;

  h1{
    margin: 0;
  }
}

.region-branding {
  max-width: 100%;
  padding: 20px 0;
  margin: 0 auto;

  @media #{$tablet} {
    max-width: $container-sm;
  }

  @media #{$normal} {
    max-width: (($container-md - $grid-gutter-width));
  }

  @media #{$wide} {
    max-width: $container-lg;
  }
}

.navbar-brand {
  font-size: 40px;
  display: inline;
  float: none;
  font-family: $font-family-demicond;
  color: $brand-primary;
  text-transform: uppercase;
  padding: 0;
  line-height: 1.2em;

  @media #{$normal} {
    font-size: 42px;
  }
}

p.navbar-text {
  display: block;
  clear: both;
  float: none;
  font-size: $font-size-h4;
  margin: 10px 0 0;
}
