.paragraph--type--statistics-block{
  margin: 6em auto;
  max-width: 900px;
  width: 100%;
  @media #{$tablet} {
    width: 50%;
  }
  @media #{$normal} {
    width: 100%;
    margin: 0 auto 8em;
  }
}

.diagonal-header{
  background: url(/sites/vpr/themes/uvatemplate/images/hp-grayhorizline.png) repeat-x center center;
  background-size: 5px auto;
  margin-bottom: 3em;
}

h2.diagonal-title {
    display: inline-block;
    line-height: 1;
    text-transform: uppercase;
    text-align: center;
    background: #fff;
    border-left: #979797 solid 1px;
    border-right: #979797 solid 1px;
    left: 50%;
    letter-spacing: 0.24375rem;
    position: relative;
    -moz-transform: skew(-20deg) translateX(-50%);
    -ms-transform: skew(-20deg translateX(-50%));
    -o-transform: skew(-20deg translateX(-50%));
    -webkit-transform: skew(-20deg) translateX(-50%);
    transform: skew(-20deg) translateX(-50%);
}

h2.diagonal-title div,
h2.diagonal-title span {
  display: block;
  padding: 0.375rem 2rem;
  -moz-transform: skew(20deg);
  -ms-transform: skew(20deg);
  -o-transform: skew(20deg);
  -webkit-transform: skew(20deg);
  transform: skew(20deg);
}

.stat-container {
  display: flex;
  flex-wrap: wrap;
  @media #{$normal} {
    flex-wrap: nowrap;
    justify-content: space-evenly;
	}
}

.stat-group {
  margin-right: 1.25em;
  margin-bottom: 4em;
  padding-left: 1em;
  border-width: 0px 0 0px 16px;
  border-style: solid;
  -webkit-border-image: -webkit-gradient(linear, 100% 0, 0 0, from(#E57200), to(#F07676)) 1 100%;
  -webkit-border-image: -webkit-linear-gradient(bottom, #E57200 0%, #F07676) 1 100%;
  -moz-border-image: -moz-linear-gradient(bottom, #E57200 0%, #F07676) 1 100%;
  -o-border-image: -o-linear-gradient(bottom, #E57200 0%, #F07676) 1 100%;
  border-image: linear-gradient(to bottom, #E57200 0%, #F07676) 1 100%;

  @media #{$normal} {
    margin-bottom: 0;
  }



  .field--name-field-caption,
  .field--name-field-caption-0,
  .field--name-field-caption-1,
  .field--name-field-caption-2,
  .field--name-field-caption-3

  {
    font-family: "franklin-gothic-urw",sans-serif;
    font-size: 2.125rem;
    font-weight: 300;
    line-height: 1.2;
  }

}
.stat-header{
  color: #232D4B;
  font-family: $font-family-demicompressed;
  font-size: 10.625rem;
  font-weight: 700;
  line-height: 0.625;
  text-transform: uppercase;
  line-height: 1;

  sup{

  }

  .field--name-field-suffix-low,
  .field--name-field-suffix-low-0,
  .field--name-field-suffix-low-1{
    font-size: 5.3125rem;
  }

  .field--name-field-suffix-high,
  .field--name-field-suffix-high-0,
  .field--name-field-suffix-high-1{
    display: none;
  }

  div{
    display: inline;
    // margin-right: -20px;
  }
}

.page-node-type-page .region-content .field--name-field-caption{
  font-style: normal
}
