/************************************
 * Subpage Sidebar Menu
 ***********************************/


aside.col-sm-3 {

	padding-left:20px;

	@media #{$tablet} {
		padding-left:0;
	}

	.region-sidebar-first {
		font-size:24px;
		font-family:$font-family-demicond;

		ul {
			margin:0 5px 20px;
		}
		h2 {
			margin-top:0;
			margin-bottom:15px;
			padding-bottom:10px;
			color:$brand-contrast;
			font-size:24px;

			background: url("/sites/all/themes/global/images/orange-dot-menu.png") repeat-x,
				url("/sites/all/themes/global/images/caret-down-orange.png") no-repeat;
			background-position: bottom left, 95% 55%;
			background-size: auto, 20px;
	    	cursor: default;

			@media #{$tablet} {
				padding-bottom:20px;
				background:0 none;
				background: url("/sites/all/themes/global/images/orange-dot-menu.png") repeat-x left bottom;
			}
		}

		@media #{$normal} {
	      padding-left:10px;
	      padding-right:40px;
	    }
		@media #{$wide} {
	      padding-left:0;
	      padding-right:60px;

	    }

	    .navbar-nav {

			li {
				display:block;
				width:100%;

				a {
					padding:12px 0;
					margin:0;

					&:hover {
						background:none;
						color:$brand-contrast;
						text-decoration: underline;
					}
				}
				&.active {
					a {
						color:$brand-contrast;
					}
				}
			}
		}
	}
	.region-sidebar-second {

		@media #{$normal} {
			padding-left:40px;
		}

	}
}
