/***********************
** People Listing Page
************************/

.view-people-page {
	.views-row {
	    background-color: #f1f1ef;
	    overflow: hidden;
	    padding: 15px;
	    margin:0 0 30px;
	    width:100%;
	    overflow:hidden;
	    h2 {
	    	color: $brand-primary;
		    font-family: $font-family-demicond;
		    font-size: 36px;
		    text-transform: uppercase;
	    }
	    .views-field-field-photo, .field--name-field-media-image {
		    background-color: white;
		    float: left;
		    margin: 0 30px 10px 0;
		    padding: 5px 5px;
		}
	}


	@media #{$normal} {
		.views-row {
		    width: calc(48% - 10px);
		    float:left;
		    margin:0 30px 30px 0;

		    &:nth-child(even) {
			    margin-right:0;
			}
		}
	}
	@media #{$wide} {
		.views-row {
		    margin:0 30px 30px 0;
		    width:48%;

		    &:nth-child(even) {
			    margin-right:0;
			}
		}
	}
}


/***********************
** People Node page
************************/

.page-node-type-people {

	.main-container {
		.group-left, .group-right {
			width:100%;
			margin-bottom:40px;
			float:none;
		}
		.field--name-field-position {
			text-transform:uppercase;
			font-size:18px;
			padding-bottom:10px;
		}

		.field--name-body {
			margin:30px 0;
		}

		@media #{$normal} {
			.group-left {
				width:auto;
				float:left;
				margin-right:50px;

				.field--name-field-photo,
				.field--name-field-media-image {
					max-width:300px;
				}
			}
			.group-right {
				width:60%;
				float:left;
			}
		}
		@media #{$wide} {

			.group-right {
				width:60%;
				
			}
		}
	}
}
